* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.body-index {
  min-height: 100vh;
  background: url("js-background-white.ac6c051d.jpg") center / cover no-repeat;
}

.body-timer {
  min-height: 100vh;
  background: url("wall.d5b323c4.jpg") center / cover no-repeat;
}

.body-promises {
  min-height: 100vh;
  background: url("js-background.77f9df3b.jpg") center / cover no-repeat;
}

ul {
  list-style: none;
}

a {
  color: #4682b4;
  background-color: rgba(255, 255, 255, .1);
  border: 1px solid #4682b4;
  border-radius: 25px;
  flex-direction: column;
  margin: 10px;
  padding: 5px 10px;
  text-decoration: none;
  display: inline-flex;
}

a:hover {
  color: #fff;
  background-color: #4682b4;
  border: 1px solid #fff;
}

.btn-white {
  background-color: #fff;
}

img {
  max-width: 100%;
  display: block;
}

.custom-btn {
  width: 130px;
  height: 40px;
  color: #6495ed;
  cursor: pointer;
  background: none;
  border-radius: 50px;
  margin: 20px;
  padding: 10px 25px;
  font-family: Lato, sans-serif;
  font-size: 16px;
  font-weight: 500;
  display: inline-block;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), inset -7px -7px 10px rgba(0, 0, 0, .1), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.timer-wrap {
  flex-direction: row;
  justify-content: center;
  gap: 30px;
  display: flex;
}

.picker {
  height: 50px;
  text-align: center;
  cursor: pointer;
  background-color: #21ebff;
  border: 2px solid #fff352;
  border-radius: 8px;
  outline: none;
  margin-bottom: 100px;
  margin-left: 10px;
  font-size: large;
  font-weight: 700;
}

.picker:disabled {
  background-color: #ff9aff;
  border: 2px solid #21ebff;
  outline: none;
}

.timer {
  flex-direction: row;
  justify-content: center;
  gap: 30px;
  display: flex;
}

.field {
  text-transform: uppercase;
  width: 140px;
  height: 140px;
  background-color: #fff352;
  border: 2px solid #21ebff;
  border-radius: 50%;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  padding-top: 15px;
  font-size: x-large;
  font-weight: 700;
  display: flex;
}

.value {
  color: #ff9aff;
  font-size: 50px;
  font-weight: 700;
}

.value.end {
  color: #21ebff;
}

.label {
  color: #4682b4;
  font-size: 15px;
  font-weight: 700;
}

.btn-start {
  width: 140px;
  height: 50px;
  color: #4682b4;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #fff352;
  border: 2px solid #21ebff;
  border-radius: 8px;
  margin: 0 20px;
  font-size: large;
  font-weight: 700;
  box-shadow: 5px 5px 1px 1px rgba(76, 86, 106, .2);
}

.btn-start:disabled {
  color: #4682b4;
  box-shadow: none;
  background-color: #ff9aff;
  border: 2px solid #fff352;
  margin: 0 20px;
}

.btn-reload {
  width: 180px;
  height: 50px;
  cursor: pointer;
  background-color: #fff352;
  border: 2px solid #ff9aff;
  border-radius: 8px;
  font-size: large;
  font-weight: 700;
}

.form {
  width: 400px;
  background-color: rgba(70, 130, 180, .6);
  border: 3px solid #4682b4;
  border-radius: 4px;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  padding: 12px;
  display: flex;
}

.form label {
  color: #fff;
  border-radius: 4px;
  flex-direction: column;
  margin-bottom: 16px;
  font-size: large;
  font-weight: 700;
  display: flex;
}

.form button {
  font: inherit;
  cursor: pointer;
  border: 2px solid #4682b4;
  border-radius: 4px;
  padding: 8px 12px;
  font-size: large;
  font-weight: 700;
}

.form input {
  border: 2px #4682b4;
  border-radius: 4px;
  outline: none;
  padding: 8px 12px;
}

